

.Buy .images img {
    width: 250px;
    height: 250px;
    margin: 10px;
    border:1px solid whitesmoke;
    border-radius: 10px;
    text-align: center;
}
.Buy .count button{
    height: 20px;
    width: 20px;
    margin: 10px;
    margin-right: 100px;
}
.Buy .count .countState{
    margin-right: 60px;
}
.Buy .description{
    height: 120px;
   

}
.Buy .paymentCart{
    display: flex;
    align-items: center;
}
.Buy .paymentCart a{
    text-decoration: none;
    font-size: 20px;
    margin: 20px;
    text-align: center;
    background:#F9EBEA;
    border-radius: 30px;
    color: black;
   

}
@media screen and (max-width:900px){
    .Buy .NoDisplay{
        display: none;
    }
}